export async function getPermissions() {
    const serverMenus = await getServerMenus();
    return serverMenus.filter(item => item.type === 2)
        .map(item => item.code);
}

let CACHE_AJAX; // ajaxpromise
function getServerMenus() {
    // 
    if (isLoginPage()) return [];

    const run = () => {
        CACHE_AJAX = CACHE_AJAX || ajax.get('/user/menus', {userId: getLoginUser()?.id})
            .then(res => (res || []).map(item => ({
                ...item,
                id: '' + item.id, // 
                parentId: '' + item.parentId,
            })));
        return CACHE_AJAX;
    };

    // MockmockmocksetTimeout
    if (process.env.NODE_ENV === 'development' && process.env.REACT_APP_MOCK) {
        return new Promise((resolve, reject) => setTimeout(() => run().then(resolve, reject)));
    }

    return run();
}
